.icon-navigation {
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
    position: absolute;
    top: 50%;
    right: 40px;
    z-index: 6;
    transform: translateY(-50%);
    width: 19px;
    height: 32px;
  }
  @media screen and (min-width: 1200px) {
    right: 55px;
  }
}

.slider-navigation {
  display: none;
  @media screen and (min-width: 768px) {
    display: inline-block;
    position: absolute;
    right: 40px;
    bottom: 40px;
    z-index: 6;

    font-weight: 600;
    font-size: 11px;
    line-height: 1.364;
    letter-spacing: 0.1em;
  }
  @media screen and (min-width: 1200px) {
    right: 55px;
  }
}

.button-slider {
  display: none;
  @media screen and (min-width: 768px) {
    display: inline-block;
    width: 60px;
    padding-bottom: 8px;
    border-bottom: 1px solid $special-text-color;
    color: $special-text-color;
    width: 60px;
    cursor: pointer;
    transition: color 250ms ease;

    &:hover,
    &:focus {
      color: #ffffff;
      border-bottom: 1px solid #ffffff;
    }

    &:first-child {
      padding-right: 22px;
      margin-right: 26px;
    }
    &:last-child {
      padding-left: 13px;
      text-align: right;
    }
  }
}

